export {}; // This makes the file a module

declare global {
    interface MediaTrackConstraintSet {
        focusMode?: ConstrainDOMString;
    }

    class ImageCapture {
        constructor(track: MediaStreamTrack);
        takePhoto(): Promise<Blob>;
    }
}

let videoStream: MediaStream | null = null;

Office.onReady(function () {
    const captureButton = document.getElementById("capturePhotoButton");
    if (captureButton) {
        captureButton.addEventListener("click", capturePhoto);
    }

    const stopButton = document.createElement("button");
    stopButton.textContent = "Stop Camera";
    document.body.appendChild(stopButton);
    stopButton.addEventListener("click", stopCamera);

    // Create and populate the camera dropdown
    const cameraDropdown = document.createElement("select");
    cameraDropdown.id = "cameraDropdown";
    document.body.appendChild(cameraDropdown);
    populateCameraDropdown();

    // Create resolution dropdown
    const resolutionDropdown = document.createElement("select");
    resolutionDropdown.id = "resolutionDropdown";
    document.body.appendChild(resolutionDropdown);
    populateResolutionDropdown();

    // Create status message element
    const statusMessage = document.createElement("div");
    statusMessage.id = "statusMessage";
    document.body.appendChild(statusMessage);
});

async function populateCameraDropdown() {
    const cameraDropdown = document.getElementById("cameraDropdown") as HTMLSelectElement;
    const devices = await navigator.mediaDevices.enumerateDevices();
    const videoDevices = devices.filter(device => device.kind === 'videoinput');
    
    videoDevices.forEach((device, index) => {
        const option = document.createElement("option");
        option.value = device.deviceId;
        option.text = device.label || `Camera ${index + 1}`;
        cameraDropdown.appendChild(option);
    });
}

function populateResolutionDropdown() {
    const resolutionDropdown = document.getElementById("resolutionDropdown") as HTMLSelectElement;
    const resolutions = [
        { width: 640, height: 480, label: "VGA" },
        { width: 1280, height: 720, label: "HD" },
        { width: 1920, height: 1080, label: "Full HD" },
        { width: 3712, height: 2784, label: "4K" }
    ];

    resolutions.forEach(res => {
        const option = document.createElement("option");
        option.value = `${res.width}x${res.height}`;
        option.text = `${res.label} (${res.width}x${res.height})`;
        resolutionDropdown.appendChild(option);
    });
}

function updateStatus(message: string) {
    const statusElement = document.getElementById("statusMessage");
    if (statusElement) {
        statusElement.textContent = message;
    }
}

async function capturePhoto() {
    let videoElement = document.querySelector("video") as HTMLVideoElement | null;
    if (!videoElement) {
        videoElement = document.createElement("video");
        document.body.appendChild(videoElement);
    }
    const captureButton = document.getElementById("capturePhotoButton");
    if (!captureButton) {
        console.error("Capture button not found");
        return;
    }
    if (!videoStream) {
        try {
            const cameraDropdown = document.getElementById("cameraDropdown") as HTMLSelectElement;
            const resolutionDropdown = document.getElementById("resolutionDropdown") as HTMLSelectElement;
            
            if (!cameraDropdown || !resolutionDropdown) {
                console.error("Dropdowns not found");
                return;
            }

            const selectedDeviceId = cameraDropdown.value;
            const [width, height] = resolutionDropdown.value.split('x').map(Number);

            updateStatus("Accessing camera...");
            const stream = await navigator.mediaDevices.getUserMedia({
                video: {
                    deviceId: { exact: selectedDeviceId },
                    width: { ideal: width },
                    height: { ideal: height },
                    advanced: [{
                        focusMode: { ideal: "continuous" }
                    }]
                } as MediaTrackConstraints
            });
            videoStream = stream;
            videoElement.srcObject = stream;
            await videoElement.play();
            updateStatus("Camera ready. Adjusting focus...");

            // Allow time for autofocus
            await new Promise(resolve => setTimeout(resolve, 2000));
            updateStatus("Ready to capture!");
        } catch (error) {
            console.error("Error accessing the camera", error);
            updateStatus("Error accessing the camera. Please try again.");
        }
    }

    captureButton.onclick = async function () {
        if (videoStream) {
            updateStatus("Capturing photo...");
            const track = videoStream.getVideoTracks()[0];
            const imageCapture = new ImageCapture(track);
    
            try {
                const photoBlob = await imageCapture.takePhoto();
                const imageDataUrl = await blobToDataURL(photoBlob);
                console.log("Captured image data URL:", imageDataUrl);
                insertImageToSlide(imageDataUrl.split(',')[1]);
                updateStatus("Photo captured and inserted!");
            } catch (error) {
                console.error("Error capturing photo", error);
                updateStatus("Error capturing photo. Please try again.");
            }
        } else {
            console.error("No video stream available");
            updateStatus("Camera not ready. Please start the camera first.");
        }
    };
}

function blobToDataURL(blob: Blob): Promise<string> {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => resolve(reader.result as string);
        reader.onerror = reject;
        reader.readAsDataURL(blob);
    });
}

function stopCamera() {
    if (videoStream) {
        videoStream.getTracks().forEach(track => track.stop());
        videoStream = null;
        const videoElement = document.querySelector("video");
        if (videoElement) {
            videoElement.remove();
        }
        updateStatus("Camera stopped.");
    }
}

function insertImageToSlide(imageDataUrl: string) {
    Office.context.document.setSelectedDataAsync(imageDataUrl, { coercionType: Office.CoercionType.Image }, function (asyncResult) {
        if (asyncResult.status === Office.AsyncResultStatus.Failed) {
            console.error("Error inserting image: " + asyncResult.error.message);
            updateStatus("Failed to insert image. Please try again.");
        } else {
            console.log("Image inserted successfully.");
            updateStatus("Image inserted successfully!");
        }
    });
}